import { useState } from 'react'
import { Link } from 'react-router-dom'
import { Plus, Search, Server, Globe, Database, MoreVertical } from 'lucide-react'
import { Menu, Transition } from '@headlessui/react'
import { Fragment } from 'react'

const Resources = () => {
  const [searchTerm, setSearchTerm] = useState('')

  const resources = [
    {
      id: 1,
      name: 'Web Server',
      url: 'https://example.com',
      type: 'HTTP',
      status: 'online',
      responseTime: '45ms',
      lastCheck: '2 мин назад',
      icon: Globe,
    },
    {
      id: 2,
      name: 'API Gateway',
      url: 'https://api.example.com',
      type: 'HTTP',
      status: 'online',
      responseTime: '32ms',
      lastCheck: '1 мин назад',
      icon: Server,
    },
    {
      id: 3,
      name: 'Database',
      url: 'db.example.com:5432',
      type: 'TCP',
      status: 'warning',
      responseTime: '120ms',
      lastCheck: '3 мин назад',
      icon: Database,
    },
    {
      id: 4,
      name: 'CDN',
      url: 'https://cdn.example.com',
      type: 'HTTP',
      status: 'offline',
      responseTime: 'N/A',
      lastCheck: '15 мин назад',
      icon: Globe,
    },
  ]

  const filteredResources = resources.filter(resource =>
    resource.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    resource.url.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'online':
        return 'bg-green-100 text-green-800'
      case 'warning':
        return 'bg-yellow-100 text-yellow-800'
      case 'offline':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'online':
        return 'Онлайн'
      case 'warning':
        return 'Проблемы'
      case 'offline':
        return 'Офлайн'
      default:
        return 'Неизвестно'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Ресурсы</h1>
          <p className="text-muted mt-1">Управление мониторингом ваших сервисов</p>
        </div>
        <Link
          to="/resources/new"
          className="btn-primary flex items-center space-x-2 mt-4 sm:mt-0"
        >
          <Plus size={16} />
          <span>Добавить ресурс</span>
        </Link>
      </div>

      {/* Search */}
      <div className="relative">
        <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
          <Search className="h-5 w-5 text-grayText" />
        </div>
        <input
          type="text"
          placeholder="Поиск ресурсов..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="input pl-10"
        />
      </div>

      {/* Resources Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredResources.map((resource) => {
          const Icon = resource.icon
          return (
            <div key={resource.id} className="card hover:shadow-md transition-shadow">
              <div className="flex items-start justify-between">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-blue-50 rounded-lg">
                    <Icon className="h-5 w-5 text-blue-600" />
                  </div>
                  <div>
                    <h3 className="font-semibold text-gray-900">{resource.name}</h3>
                    <p className="text-sm text-muted">{resource.type}</p>
                  </div>
                </div>
                <Menu as="div" className="relative">
                  <Menu.Button className="p-1 rounded-md hover:bg-gray-100">
                    <MoreVertical className="h-4 w-4 text-grayText" />
                  </Menu.Button>
                  <Transition
                    as={Fragment}
                    enter="transition ease-out duration-100"
                    enterFrom="transform opacity-0 scale-95"
                    enterTo="transform opacity-100 scale-100"
                    leave="transition ease-in duration-75"
                    leaveFrom="transform opacity-100 scale-100"
                    leaveTo="transform opacity-0 scale-95"
                  >
                    <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                      <Menu.Item>
                        {({ active }) => (
                          <Link
                            to={`/resources/${resource.id}`}
                            className={`${
                              active ? 'bg-gray-100' : ''
                            } block px-4 py-2 text-sm text-gray-700`}
                          >
                            Подробнее
                          </Link>
                        )}
                      </Menu.Item>
                      <Menu.Item>
                        {({ active }) => (
                          <button
                            className={`${
                              active ? 'bg-gray-100' : ''
                            } block w-full text-left px-4 py-2 text-sm text-gray-700`}
                          >
                            Редактировать
                          </button>
                        )}
                      </Menu.Item>
                      <Menu.Item>
                        {({ active }) => (
                          <button
                            className={`${
                              active ? 'bg-gray-100' : ''
                            } block w-full text-left px-4 py-2 text-sm text-red-600`}
                          >
                            Удалить
                          </button>
                        )}
                      </Menu.Item>
                    </Menu.Items>
                  </Transition>
                </Menu>
              </div>

              <div className="mt-4 space-y-2">
                <p className="text-sm text-muted break-all">{resource.url}</p>
                <div className="flex items-center justify-between">
                  <span
                    className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(
                      resource.status
                    )}`}
                  >
                    {getStatusText(resource.status)}
                  </span>
                  <span className="text-sm text-muted">{resource.responseTime}</span>
                </div>
                <p className="text-xs text-muted">Последняя проверка: {resource.lastCheck}</p>
              </div>

              <div className="mt-4">
                <Link
                  to={`/resources/${resource.id}`}
                  className="text-sm text-blue-600 hover:text-blue-500 font-medium"
                >
                  Подробнее →
                </Link>
              </div>
            </div>
          )
        })}
      </div>

      {filteredResources.length === 0 && (
        <div className="text-center py-12">
          <Server className="mx-auto h-12 w-12 text-grayText" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">Ресурсы не найдены</h3>
          <p className="mt-1 text-sm text-muted">
            {searchTerm ? 'Попробуйте изменить поисковый запрос' : 'Добавьте первый ресурс для мониторинга'}
          </p>
        </div>
      )}
    </div>
  )
}

export default Resources

