import { Routes, Route } from 'react-router-dom'
import Layout from './components/Layout'
import ProtectedRoute from './components/ProtectedRoute'
import HomePage from './pages/HomePage'
import AuthPage from './pages/AuthPage'
import Home from './pages/Home'
import Resources from './pages/Resources'
import ResourcesPage from './pages/ResourcesPage'
import ResourceDetail from './pages/ResourceDetail'
import ResourceCabinet from './pages/ResourceCabinet'
import Observations from './pages/Observations'
import ObservationsPage from './pages/ObservationsPage'
import Cabinet from './pages/Cabinet'
import UserCabinetPage from './pages/UserCabinetPage'

function App() {
  return (
    <Routes>
      {/* Public routes */}
      <Route path="/" element={<HomePage />} />
      <Route path="/auth" element={<AuthPage />} />
      
      {/* Protected app routes */}
      <Route path="/app" element={
        <ProtectedRoute>
          <Layout />
        </ProtectedRoute>
      }>
        <Route index element={<Home />} />
        <Route path="resources" element={<ResourcesPage />} />
        <Route path="resources/:id" element={<ResourceCabinet />} />
        <Route path="observations" element={<ObservationsPage />} />
        <Route path="cabinet" element={<UserCabinetPage />} />
      </Route>
      
      {/* Legacy routes for backward compatibility */}
      <Route path="/dashboard" element={
        <ProtectedRoute>
          <Layout><Home /></Layout>
        </ProtectedRoute>
      } />
      <Route path="/login" element={<AuthPage />} />
      <Route path="/old-resources" element={
        <ProtectedRoute>
          <Layout><Resources /></Layout>
        </ProtectedRoute>
      } />
      <Route path="/old-resources/:id" element={
        <ProtectedRoute>
          <Layout><ResourceDetail /></Layout>
        </ProtectedRoute>
      } />
      <Route path="/old-observations" element={
        <ProtectedRoute>
          <Layout><Observations /></Layout>
        </ProtectedRoute>
      } />
      <Route path="/old-cabinet" element={
        <ProtectedRoute>
          <Layout><Cabinet /></Layout>
        </ProtectedRoute>
      } />
    </Routes>
  )
}

export default App
