import { useParams } from 'react-router-dom'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'
import { ArrowLeft, Globe, Clock, Activity, AlertCircle } from 'lucide-react'
import { Link } from 'react-router-dom'

const ResourceDetail = () => {
  const { id } = useParams()

  // Mock data - в реальном приложении данные будут загружаться по ID
  const resource = {
    id: id,
    name: 'Web Server',
    url: 'https://example.com',
    type: 'HTTP',
    status: 'online',
    responseTime: '45ms',
    uptime: '99.9%',
    lastCheck: '2 мин назад',
  }

  const chartData = [
    { time: '00:00', responseTime: 42 },
    { time: '04:00', responseTime: 38 },
    { time: '08:00', responseTime: 45 },
    { time: '12:00', responseTime: 52 },
    { time: '16:00', responseTime: 48 },
    { time: '20:00', responseTime: 41 },
    { time: '24:00', responseTime: 39 },
  ]

  const incidents = [
    {
      id: 1,
      time: '2024-01-15 14:30',
      duration: '5 мин',
      description: 'Высокое время отклика',
      severity: 'warning',
    },
    {
      id: 2,
      time: '2024-01-14 09:15',
      duration: '2 мин',
      description: 'Сервер недоступен',
      severity: 'error',
    },
  ]

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'online':
        return 'bg-green-100 text-green-800'
      case 'warning':
        return 'bg-yellow-100 text-yellow-800'
      case 'offline':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'online':
        return 'Онлайн'
      case 'warning':
        return 'Проблемы'
      case 'offline':
        return 'Офлайн'
      default:
        return 'Неизвестно'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center space-x-4">
        <Link
          to="/resources"
          className="p-2 rounded-md hover:bg-gray-100 transition-colors"
        >
          <ArrowLeft className="h-5 w-5 text-grayText" />
        </Link>
        <div>
          <h1 className="text-2xl font-bold text-gray-900">{resource.name}</h1>
          <p className="text-muted">{resource.url}</p>
        </div>
      </div>

      {/* Status Overview */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="card">
          <div className="flex items-center space-x-3">
            <div className="p-2 bg-blue-50 rounded-lg">
              <Globe className="h-5 w-5 text-blue-600" />
            </div>
            <div>
              <p className="text-sm text-muted">Статус</p>
              <span
                className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-sm font-medium ${getStatusColor(
                  resource.status
                )}`}
              >
                {getStatusText(resource.status)}
              </span>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center space-x-3">
            <div className="p-2 bg-green-50 rounded-lg">
              <Activity className="h-5 w-5 text-green-600" />
            </div>
            <div>
              <p className="text-sm text-muted">Время отклика</p>
              <p className="text-lg font-semibold text-gray-900">{resource.responseTime}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center space-x-3">
            <div className="p-2 bg-purple-50 rounded-lg">
              <Clock className="h-5 w-5 text-purple-600" />
            </div>
            <div>
              <p className="text-sm text-muted">Время работы</p>
              <p className="text-lg font-semibold text-gray-900">{resource.uptime}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center space-x-3">
            <div className="p-2 bg-orange-50 rounded-lg">
              <AlertCircle className="h-5 w-5 text-orange-600" />
            </div>
            <div>
              <p className="text-sm text-muted">Последняя проверка</p>
              <p className="text-sm font-medium text-gray-900">{resource.lastCheck}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Response Time Chart */}
      <div className="card">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Время отклика за 24 часа</h3>
        <div className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#E5E5E5" />
              <XAxis dataKey="time" stroke="#9CA3AF" />
              <YAxis stroke="#9CA3AF" />
              <Tooltip
                contentStyle={{
                  backgroundColor: 'white',
                  border: '1px solid #E5E5E5',
                  borderRadius: '8px',
                }}
              />
              <Line
                type="monotone"
                dataKey="responseTime"
                stroke="#3B82F6"
                strokeWidth={2}
                dot={{ fill: '#3B82F6', strokeWidth: 2, r: 4 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Recent Incidents */}
      <div className="card">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Последние инциденты</h3>
        {incidents.length > 0 ? (
          <div className="space-y-3">
            {incidents.map((incident) => (
              <div key={incident.id} className="flex items-center justify-between py-3 border-b border-grayLight last:border-b-0">
                <div className="flex items-center space-x-3">
                  <div
                    className={`w-3 h-3 rounded-full ${
                      incident.severity === 'error' ? 'bg-red-500' : 'bg-yellow-500'
                    }`}
                  />
                  <div>
                    <p className="text-sm font-medium text-gray-900">{incident.description}</p>
                    <p className="text-xs text-muted">{incident.time}</p>
                  </div>
                </div>
                <span className="text-sm text-muted">{incident.duration}</span>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-8">
            <AlertCircle className="mx-auto h-8 w-8 text-grayText" />
            <p className="mt-2 text-sm text-muted">Инцидентов не обнаружено</p>
          </div>
        )}
      </div>
    </div>
  )
}

export default ResourceDetail

