import { useState } from 'react'
import { Tab } from '@headlessui/react'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, BarChart, Bar } from 'recharts'
import { Activity, AlertTriangle, CheckCircle, Clock } from 'lucide-react'

const Observations = () => {
  const [selectedPeriod, setSelectedPeriod] = useState('24h')

  const responseTimeData = [
    { time: '00:00', value: 42 },
    { time: '04:00', value: 38 },
    { time: '08:00', value: 45 },
    { time: '12:00', value: 52 },
    { time: '16:00', value: 48 },
    { time: '20:00', value: 41 },
    { time: '24:00', value: 39 },
  ]

  const uptimeData = [
    { day: 'Пн', uptime: 99.9 },
    { day: 'Вт', uptime: 99.8 },
    { day: 'Ср', uptime: 99.7 },
    { day: 'Чт', uptime: 99.9 },
    { day: 'Пт', uptime: 99.6 },
    { day: 'Сб', uptime: 99.9 },
    { day: 'Вс', uptime: 99.8 },
  ]

  const alerts = [
    {
      id: 1,
      type: 'warning',
      title: 'Высокое время отклика',
      resource: 'Web Server',
      time: '2 мин назад',
      description: 'Время отклика превышает 100ms',
    },
    {
      id: 2,
      type: 'error',
      title: 'Сервер недоступен',
      resource: 'API Gateway',
      time: '15 мин назад',
      description: 'Сервер не отвечает на запросы',
    },
    {
      id: 3,
      type: 'info',
      title: 'Восстановление',
      resource: 'Database',
      time: '1 час назад',
      description: 'Сервер восстановлен после сбоя',
    },
  ]

  const getAlertIcon = (type: string) => {
    switch (type) {
      case 'error':
        return <AlertTriangle className="h-5 w-5 text-red-500" />
      case 'warning':
        return <AlertTriangle className="h-5 w-5 text-yellow-500" />
      case 'info':
        return <CheckCircle className="h-5 w-5 text-blue-500" />
      default:
        return <Clock className="h-5 w-5 text-gray-500" />
    }
  }

  const getAlertColor = (type: string) => {
    switch (type) {
      case 'error':
        return 'border-red-200 bg-red-50'
      case 'warning':
        return 'border-yellow-200 bg-yellow-50'
      case 'info':
        return 'border-blue-200 bg-blue-50'
      default:
        return 'border-gray-200 bg-gray-50'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Наблюдения</h1>
          <p className="text-muted mt-1">Анализ производительности и инцидентов</p>
        </div>
        <div className="flex space-x-2 mt-4 sm:mt-0">
          {['24h', '7d', '30d'].map((period) => (
            <button
              key={period}
              onClick={() => setSelectedPeriod(period)}
              className={`px-3 py-1 text-sm font-medium rounded-md transition-colors ${
                selectedPeriod === period
                  ? 'bg-blue-600 text-white'
                  : 'bg-grayLight text-gray-700 hover:bg-gray-300'
              }`}
            >
              {period}
            </button>
          ))}
        </div>
      </div>

      {/* Tabs */}
      <Tab.Group>
        <Tab.List className="flex space-x-1 rounded-xl bg-grayLight p-1">
          {['Производительность', 'Алерты', 'Статистика'].map((category) => (
            <Tab
              key={category}
              className={({ selected }) =>
                `w-full rounded-lg py-2.5 text-sm font-medium leading-5 transition-colors ${
                  selected
                    ? 'bg-white text-blue-600 shadow'
                    : 'text-grayText hover:bg-white/[0.12] hover:text-gray-900'
                }`
              }
            >
              {category}
            </Tab>
          ))}
        </Tab.List>

        <Tab.Panels className="mt-6">
          {/* Performance Tab */}
          <Tab.Panel className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Response Time Chart */}
              <div className="card">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Время отклика</h3>
                <div className="h-64">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={responseTimeData}>
                      <CartesianGrid strokeDasharray="3 3" stroke="#E5E5E5" />
                      <XAxis dataKey="time" stroke="#9CA3AF" />
                      <YAxis stroke="#9CA3AF" />
                      <Tooltip
                        contentStyle={{
                          backgroundColor: 'white',
                          border: '1px solid #E5E5E5',
                          borderRadius: '8px',
                        }}
                      />
                      <Line
                        type="monotone"
                        dataKey="value"
                        stroke="#3B82F6"
                        strokeWidth={2}
                        dot={{ fill: '#3B82F6', strokeWidth: 2, r: 4 }}
                      />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              </div>

              {/* Uptime Chart */}
              <div className="card">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Время работы</h3>
                <div className="h-64">
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={uptimeData}>
                      <CartesianGrid strokeDasharray="3 3" stroke="#E5E5E5" />
                      <XAxis dataKey="day" stroke="#9CA3AF" />
                      <YAxis stroke="#9CA3AF" />
                      <Tooltip
                        contentStyle={{
                          backgroundColor: 'white',
                          border: '1px solid #E5E5E5',
                          borderRadius: '8px',
                        }}
                      />
                      <Bar dataKey="uptime" fill="#10B981" radius={[4, 4, 0, 0]} />
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </div>
            </div>
          </Tab.Panel>

          {/* Alerts Tab */}
          <Tab.Panel>
            <div className="space-y-4">
              {alerts.map((alert) => (
                <div
                  key={alert.id}
                  className={`border rounded-lg p-4 ${getAlertColor(alert.type)}`}
                >
                  <div className="flex items-start space-x-3">
                    {getAlertIcon(alert.type)}
                    <div className="flex-1">
                      <div className="flex items-center justify-between">
                        <h4 className="text-sm font-medium text-gray-900">{alert.title}</h4>
                        <span className="text-xs text-muted">{alert.time}</span>
                      </div>
                      <p className="text-sm text-gray-600 mt-1">{alert.resource}</p>
                      <p className="text-sm text-gray-700 mt-2">{alert.description}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </Tab.Panel>

          {/* Statistics Tab */}
          <Tab.Panel>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
              <div className="card">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-green-50 rounded-lg">
                    <CheckCircle className="h-5 w-5 text-green-600" />
                  </div>
                  <div>
                    <p className="text-sm text-muted">Средний аптайм</p>
                    <p className="text-2xl font-semibold text-gray-900">99.8%</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-blue-50 rounded-lg">
                    <Activity className="h-5 w-5 text-blue-600" />
                  </div>
                  <div>
                    <p className="text-sm text-muted">Средний отклик</p>
                    <p className="text-2xl font-semibold text-gray-900">45ms</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-yellow-50 rounded-lg">
                    <AlertTriangle className="h-5 w-5 text-yellow-600" />
                  </div>
                  <div>
                    <p className="text-sm text-muted">Предупреждения</p>
                    <p className="text-2xl font-semibold text-gray-900">3</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-red-50 rounded-lg">
                    <AlertTriangle className="h-5 w-5 text-red-600" />
                  </div>
                  <div>
                    <p className="text-sm text-muted">Критические</p>
                    <p className="text-2xl font-semibold text-gray-900">1</p>
                  </div>
                </div>
              </div>
            </div>
          </Tab.Panel>
        </Tab.Panels>
      </Tab.Group>
    </div>
  )
}

export default Observations

