import { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import { ArrowLeft, CheckCircle, XCircle, Loader2, Lightbulb } from 'lucide-react'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'
import { Tooltip as ReactTooltip } from 'react-tooltip'
import Header from '../components/Header'
import { useResourcesStore } from '../store/resourcesStore'

const ResourceCabinet = () => {
  const { id } = useParams<{ id: string }>()
  const [isLoading, setIsLoading] = useState(true)
  const { resources } = useResourcesStore()
  
  const resource = resources.find(r => r.id === id)

  // Mock данные для графика сбоев
  const errorChartData = [
    { time: '12:00', errors: 1 },
    { time: '13:00', errors: 0 },
    { time: '14:00', errors: 2 },
    { time: '15:00', errors: 0 },
    { time: '16:00', errors: 1 },
    { time: '17:00', errors: 0 },
    { time: '18:00', errors: 3 },
    { time: '19:00', errors: 0 },
    { time: '20:00', errors: 1 },
    { time: '21:00', errors: 0 },
  ]

  useEffect(() => {
    // Имитация загрузки данных
    const timer = setTimeout(() => {
      setIsLoading(false)
    }, 1000)

    return () => clearTimeout(timer)
  }, [])

  if (isLoading) {
    return (
      <div className="min-h-screen bg-milky">
        <Header />
        <div className="container py-6">
          <div className="flex items-center justify-center py-12">
            <Loader2 className="h-8 w-8 animate-spin text-navyDark" />
          </div>
        </div>
      </div>
    )
  }

  if (!resource) {
    return (
      <div className="min-h-screen bg-milky">
        <Header />
        <div className="container py-6">
          <div className="text-center py-12">
            <h1 className="text-2xl font-montserrat font-bold text-navyDark mb-4">Ресурс не найден</h1>
            <Link to="/app/resources" className="btn-primary">
              Вернуться к ресурсам
            </Link>
          </div>
        </div>
      </div>
    )
  }

  const getStatusIcon = (status: 'online' | 'offline') => {
    if (status === 'online') {
      return <CheckCircle className="h-4 w-4 text-green-500" />
    }
    return <XCircle className="h-4 w-4 text-red-500" />
  }

  const getStatusTooltip = (status: 'online' | 'offline') => {
    return status === 'online' ? 'Доступен' : 'Сбой'
  }

  return (
    <div className="min-h-screen bg-milky">
      <Header />
      
      <div className="container py-6">
        {/* Header */}
        <div className="flex items-center space-x-4 mb-6">
          <Link
            to="/app/resources"
            className="p-2 rounded-md hover:bg-silverGrad hover:brightness-110 transition-all duration-300"
          >
            <ArrowLeft className="h-5 w-5 text-midGray" />
          </Link>
          <div>
            <h1 className="text-2xl font-montserrat font-bold text-navyDark">{resource.name}</h1>
            <p className="text-midGray font-inter">{resource.url}</p>
          </div>
        </div>

        {/* Error Chart */}
        <div className="chart-container mb-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-inter font-semibold text-navyDark">График сбоев</h2>
            <div className="flex items-center space-x-2 text-sm text-midGray font-inter">
              <Lightbulb className="h-4 w-4 text-navyDark" />
              <span>График показывает тренд сбоев — низкий = всё ок</span>
            </div>
          </div>
          <div className="h-64">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={errorChartData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#A9A9A9" strokeOpacity={0.3} />
                <XAxis 
                  dataKey="time" 
                  stroke="#000000"
                  fontSize={12}
                  fontFamily="Inter"
                />
                <YAxis 
                  stroke="#000000"
                  fontSize={12}
                  fontFamily="Inter"
                />
                <Tooltip
                  contentStyle={{
                    backgroundColor: '#F5F5F5',
                    border: '1px solid #C0C0C0',
                    borderRadius: '8px',
                    fontSize: '14px',
                    color: '#000000',
                    fontFamily: 'Inter',
                  }}
                  labelFormatter={(value) => `Время: ${value}`}
                  formatter={(value) => [`${value} сбоев`, 'Сбоев за час']}
                />
                <Line
                  type="monotone"
                  dataKey="errors"
                  stroke="url(#steelGradient)"
                  strokeWidth={3}
                  dot={{ fill: '#808080', strokeWidth: 2, r: 5 }}
                  activeDot={{ r: 7, stroke: '#C0C0C0', strokeWidth: 2, fill: '#808080' }}
                  strokeDasharray="0"
                  animationDuration={1000}
                />
                <defs>
                  <linearGradient id="steelGradient" x1="0%" y1="0%" x2="100%" y2="0%">
                    <stop offset="0%" stopColor="#808080" />
                    <stop offset="100%" stopColor="#43464B" />
                  </linearGradient>
                </defs>
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Endpoints Table */}
        <div className="bg-white rounded-xl border border-lightGray/40 overflow-hidden shadow-md">
          <div className="px-6 py-4 border-b border-lightGray/40">
            <h2 className="text-lg font-inter font-semibold text-navyDark">Эндпоинты</h2>
          </div>
          
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-lightGray">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-inter font-medium text-navyDark uppercase tracking-wider">
                    Эндпоинт
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-inter font-medium text-navyDark uppercase tracking-wider">
                    Индикатор
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-inter font-medium text-navyDark uppercase tracking-wider">
                    Сбои 24ч
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-lightGray/30">
                {resource.endpoints.map((endpoint, index) => (
                  <tr key={index} className="hover:bg-silverGrad hover:brightness-110 transition-all duration-300">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-inter font-medium text-black">
                        {endpoint.path}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center space-x-2">
                        <div
                          data-tooltip-id="status-tooltip"
                          data-tooltip-content={getStatusTooltip(endpoint.status)}
                        >
                          {getStatusIcon(endpoint.status)}
                        </div>
                        <span className="text-sm text-midGray font-inter">
                          {endpoint.status === 'online' ? 'Доступен' : 'Сбой'}
                        </span>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`text-sm font-inter font-medium ${
                        endpoint.errors24h === 0 ? 'text-green-600' : 'text-red-600'
                      }`}>
                        {endpoint.errors24h}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        {/* Resource Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-6">
          <div className="bg-white rounded-xl border border-lightGray/40 p-4 shadow-md hover:shadow-lg transition-all duration-300">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-gradient-to-r from-green-400 to-green-600 rounded-lg animate-pulse">
                <CheckCircle className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-midGray font-inter">Время работы</p>
                <p className="text-lg font-inter font-semibold text-black">{resource.uptime}%</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl border border-lightGray/40 p-4 shadow-md hover:shadow-lg transition-all duration-300">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-gradient-to-r from-red-400 to-red-600 rounded-lg animate-pulse">
                <XCircle className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-midGray font-inter">Сбои 24ч</p>
                <p className="text-lg font-inter font-semibold text-black">{resource.errors24h}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl border border-lightGray/40 p-4 shadow-md hover:shadow-lg transition-all duration-300">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-gradient-to-r from-blue-400 to-blue-600 rounded-lg animate-pulse">
                <CheckCircle className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-midGray font-inter">Активные</p>
                <p className="text-lg font-inter font-semibold text-black">{resource.active}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl border border-lightGray/40 p-4 shadow-md hover:shadow-lg transition-all duration-300">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-gradient-to-r from-purple-400 to-purple-600 rounded-lg animate-pulse">
                <CheckCircle className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-midGray font-inter">SLA</p>
                <p className="text-lg font-inter font-semibold text-black">{resource.sla}%</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Tooltips */}
      <ReactTooltip
        id="status-tooltip"
        place="top"
        style={{
          backgroundColor: '#F5F5F5',
          color: '#000000',
          borderRadius: '6px',
          fontSize: '12px',
          padding: '6px 8px',
          border: '1px solid #C0C0C0',
          fontFamily: 'Inter',
        }}
      />
    </div>
  )
}

export default ResourceCabinet
