import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import * as yup from 'yup'
import { Tab } from '@headlessui/react'
import { User, Bell, Shield, CreditCard, LogOut, Save } from 'lucide-react'

const profileSchema = yup.object({
  name: yup.string().required('Имя обязательно'),
  email: yup.string().email('Неверный формат email').required('Email обязателен'),
  company: yup.string(),
  phone: yup.string(),
})

const passwordSchema = yup.object({
  currentPassword: yup.string().required('Текущий пароль обязателен'),
  newPassword: yup.string().min(6, 'Пароль должен содержать минимум 6 символов').required('Новый пароль обязателен'),
  confirmPassword: yup.string().oneOf([yup.ref('newPassword')], 'Пароли не совпадают').required('Подтверждение пароля обязательно'),
})

type ProfileFormData = yup.InferType<typeof profileSchema>
type PasswordFormData = yup.InferType<typeof passwordSchema>

const Cabinet = () => {
  const [activeTab, setActiveTab] = useState(0)

  const profileForm = useForm<ProfileFormData>({
    resolver: yupResolver(profileSchema),
    defaultValues: {
      name: 'Иван Иванов',
      email: 'ivan@example.com',
      company: 'ООО "Пример"',
      phone: '+7 (999) 123-45-67',
    },
  })

  const passwordForm = useForm<PasswordFormData>({
    resolver: yupResolver(passwordSchema),
  })

  const onProfileSubmit = (data: ProfileFormData) => {
    console.log('Profile data:', data)
    // Здесь будет логика сохранения профиля
  }

  const onPasswordSubmit = (data: PasswordFormData) => {
    console.log('Password data:', data)
    // Здесь будет логика смены пароля
  }

  const notifications = [
    { id: 1, title: 'Email уведомления', description: 'Получать уведомления на email', enabled: true },
    { id: 2, title: 'SMS уведомления', description: 'Получать SMS при критических сбоях', enabled: false },
    { id: 3, title: 'Push уведомления', description: 'Уведомления в браузере', enabled: true },
    { id: 4, title: 'Telegram бот', description: 'Интеграция с Telegram', enabled: false },
  ]

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Личный кабинет</h1>
        <p className="text-muted mt-1">Управление профилем и настройками</p>
      </div>

      {/* Tabs */}
      <Tab.Group selectedIndex={activeTab} onChange={setActiveTab}>
        <Tab.List className="flex space-x-1 rounded-xl bg-grayLight p-1">
          {[
            { name: 'Профиль', icon: User },
            { name: 'Уведомления', icon: Bell },
            { name: 'Безопасность', icon: Shield },
            { name: 'Биллинг', icon: CreditCard },
          ].map((tab) => {
            const Icon = tab.icon
            return (
              <Tab
                key={tab.name}
                className={({ selected }) =>
                  `flex items-center space-x-2 w-full rounded-lg py-2.5 text-sm font-medium leading-5 transition-colors ${
                    selected
                      ? 'bg-white text-blue-600 shadow'
                      : 'text-grayText hover:bg-white/[0.12] hover:text-gray-900'
                  }`
                }
              >
                <Icon size={16} />
                <span className="hidden sm:inline">{tab.name}</span>
              </Tab>
            )
          })}
        </Tab.List>

        <Tab.Panels className="mt-6">
          {/* Profile Tab */}
          <Tab.Panel>
            <div className="card">
              <h3 className="text-lg font-semibold text-gray-900 mb-6">Информация о профиле</h3>
              <form onSubmit={profileForm.handleSubmit(onProfileSubmit)} className="space-y-4">
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                  <div>
                    <label htmlFor="name" className="block text-sm font-medium text-gray-700">
                      Имя
                    </label>
                    <input
                      {...profileForm.register('name')}
                      type="text"
                      className="input mt-1"
                    />
                    {profileForm.formState.errors.name && (
                      <p className="mt-1 text-sm text-red-600">
                        {profileForm.formState.errors.name.message}
                      </p>
                    )}
                  </div>

                  <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                      Email
                    </label>
                    <input
                      {...profileForm.register('email')}
                      type="email"
                      className="input mt-1"
                    />
                    {profileForm.formState.errors.email && (
                      <p className="mt-1 text-sm text-red-600">
                        {profileForm.formState.errors.email.message}
                      </p>
                    )}
                  </div>

                  <div>
                    <label htmlFor="company" className="block text-sm font-medium text-gray-700">
                      Компания
                    </label>
                    <input
                      {...profileForm.register('company')}
                      type="text"
                      className="input mt-1"
                    />
                  </div>

                  <div>
                    <label htmlFor="phone" className="block text-sm font-medium text-gray-700">
                      Телефон
                    </label>
                    <input
                      {...profileForm.register('phone')}
                      type="tel"
                      className="input mt-1"
                    />
                  </div>
                </div>

                <div className="flex justify-end">
                  <button
                    type="submit"
                    className="btn-primary flex items-center space-x-2"
                  >
                    <Save size={16} />
                    <span>Сохранить</span>
                  </button>
                </div>
              </form>
            </div>
          </Tab.Panel>

          {/* Notifications Tab */}
          <Tab.Panel>
            <div className="card">
              <h3 className="text-lg font-semibold text-gray-900 mb-6">Настройки уведомлений</h3>
              <div className="space-y-4">
                {notifications.map((notification) => (
                  <div key={notification.id} className="flex items-center justify-between py-3 border-b border-grayLight last:border-b-0">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">{notification.title}</h4>
                      <p className="text-sm text-muted">{notification.description}</p>
                    </div>
                    <label className="relative inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        defaultChecked={notification.enabled}
                        className="sr-only peer"
                      />
                      <div className="w-11 h-6 bg-grayLight peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                    </label>
                  </div>
                ))}
              </div>
            </div>
          </Tab.Panel>

          {/* Security Tab */}
          <Tab.Panel>
            <div className="space-y-6">
              {/* Change Password */}
              <div className="card">
                <h3 className="text-lg font-semibold text-gray-900 mb-6">Смена пароля</h3>
                <form onSubmit={passwordForm.handleSubmit(onPasswordSubmit)} className="space-y-4">
                  <div>
                    <label htmlFor="currentPassword" className="block text-sm font-medium text-gray-700">
                      Текущий пароль
                    </label>
                    <input
                      {...passwordForm.register('currentPassword')}
                      type="password"
                      className="input mt-1"
                    />
                    {passwordForm.formState.errors.currentPassword && (
                      <p className="mt-1 text-sm text-red-600">
                        {passwordForm.formState.errors.currentPassword.message}
                      </p>
                    )}
                  </div>

                  <div>
                    <label htmlFor="newPassword" className="block text-sm font-medium text-gray-700">
                      Новый пароль
                    </label>
                    <input
                      {...passwordForm.register('newPassword')}
                      type="password"
                      className="input mt-1"
                    />
                    {passwordForm.formState.errors.newPassword && (
                      <p className="mt-1 text-sm text-red-600">
                        {passwordForm.formState.errors.newPassword.message}
                      </p>
                    )}
                  </div>

                  <div>
                    <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                      Подтвердите пароль
                    </label>
                    <input
                      {...passwordForm.register('confirmPassword')}
                      type="password"
                      className="input mt-1"
                    />
                    {passwordForm.formState.errors.confirmPassword && (
                      <p className="mt-1 text-sm text-red-600">
                        {passwordForm.formState.errors.confirmPassword.message}
                      </p>
                    )}
                  </div>

                  <div className="flex justify-end">
                    <button
                      type="submit"
                      className="btn-primary flex items-center space-x-2"
                    >
                      <Save size={16} />
                      <span>Изменить пароль</span>
                    </button>
                  </div>
                </form>
              </div>

              {/* Logout */}
              <div className="card">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Выход из системы</h3>
                <p className="text-sm text-muted mb-4">
                  Выйти из аккаунта на всех устройствах
                </p>
                <button className="btn-secondary flex items-center space-x-2 text-red-600 hover:bg-red-50">
                  <LogOut size={16} />
                  <span>Выйти</span>
                </button>
              </div>
            </div>
          </Tab.Panel>

          {/* Billing Tab */}
          <Tab.Panel>
            <div className="card">
              <h3 className="text-lg font-semibold text-gray-900 mb-6">Биллинг</h3>
              <div className="space-y-4">
                <div className="flex items-center justify-between py-3 border-b border-grayLight">
                  <div>
                    <h4 className="text-sm font-medium text-gray-900">Текущий план</h4>
                    <p className="text-sm text-muted">Pro - 50 ресурсов</p>
                  </div>
                  <span className="text-sm font-medium text-gray-900">₽2,990/мес</span>
                </div>

                <div className="flex items-center justify-between py-3 border-b border-grayLight">
                  <div>
                    <h4 className="text-sm font-medium text-gray-900">Следующий платеж</h4>
                    <p className="text-sm text-muted">15 февраля 2024</p>
                  </div>
                  <span className="text-sm font-medium text-gray-900">₽2,990</span>
                </div>

                <div className="flex items-center justify-between py-3">
                  <div>
                    <h4 className="text-sm font-medium text-gray-900">Использовано ресурсов</h4>
                    <p className="text-sm text-muted">24 из 50</p>
                  </div>
                  <div className="w-24 bg-grayLight rounded-full h-2">
                    <div className="bg-blue-600 h-2 rounded-full" style={{ width: '48%' }}></div>
                  </div>
                </div>
              </div>

              <div className="mt-6 flex space-x-3">
                <button className="btn-primary">Изменить план</button>
                <button className="btn-secondary">История платежей</button>
              </div>
            </div>
          </Tab.Panel>
        </Tab.Panels>
      </Tab.Group>
    </div>
  )
}

export default Cabinet

