import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'
import { Activity, Server, AlertTriangle, CheckCircle } from 'lucide-react'

const Home = () => {
  const stats = [
    { name: 'Всего ресурсов', value: '24', icon: Server, change: '+2', changeType: 'positive' },
    { name: 'Активные', value: '22', icon: CheckCircle, change: '+1', changeType: 'positive' },
    { name: 'Проблемы', value: '2', icon: AlertTriangle, change: '-1', changeType: 'negative' },
    { name: 'Средний отклик', value: '45ms', icon: Activity, change: '-5ms', changeType: 'positive' },
  ]

  const chartData = [
    { name: 'Пн', value: 45 },
    { name: 'Вт', value: 52 },
    { name: 'Ср', value: 38 },
    { name: 'Чт', value: 61 },
    { name: 'Пт', value: 55 },
    { name: 'Сб', value: 48 },
    { name: 'Вс', value: 42 },
  ]

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Панель мониторинга</h1>
        <p className="text-muted mt-1">Обзор состояния ваших ресурсов</p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
        {stats.map((stat) => {
          const Icon = stat.icon
          return (
            <div key={stat.name} className="card">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-muted">{stat.name}</p>
                  <p className="text-2xl font-semibold text-gray-900">{stat.value}</p>
                </div>
                <div className="p-3 bg-blue-50 rounded-lg">
                  <Icon className="h-6 w-6 text-blue-600" />
                </div>
              </div>
              <div className="mt-4">
                <span
                  className={`text-sm font-medium ${
                    stat.changeType === 'positive' ? 'text-green-600' : 'text-red-600'
                  }`}
                >
                  {stat.change}
                </span>
                <span className="text-sm text-muted ml-1">за последние 24ч</span>
              </div>
            </div>
          )
        })}
      </div>

      {/* Chart */}
      <div className="card">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Время отклика за неделю</h3>
        <div className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#E5E5E5" />
              <XAxis dataKey="name" stroke="#9CA3AF" />
              <YAxis stroke="#9CA3AF" />
              <Tooltip
                contentStyle={{
                  backgroundColor: 'white',
                  border: '1px solid #E5E5E5',
                  borderRadius: '8px',
                }}
              />
              <Bar dataKey="value" fill="#3B82F6" radius={[4, 4, 0, 0]} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Recent Activity */}
      <div className="card">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Последние события</h3>
        <div className="space-y-3">
          {[
            { time: '2 мин назад', event: 'Сервер web-01 восстановлен', status: 'success' },
            { time: '15 мин назад', event: 'Высокое время отклика на api-02', status: 'warning' },
            { time: '1 час назад', event: 'Новый ресурс добавлен', status: 'info' },
            { time: '2 часа назад', event: 'Сервер db-01 недоступен', status: 'error' },
          ].map((activity, index) => (
            <div key={index} className="flex items-center justify-between py-2">
              <div className="flex items-center space-x-3">
                <div
                  className={`w-2 h-2 rounded-full ${
                    activity.status === 'success'
                      ? 'bg-green-500'
                      : activity.status === 'warning'
                      ? 'bg-yellow-500'
                      : activity.status === 'error'
                      ? 'bg-red-500'
                      : 'bg-blue-500'
                  }`}
                />
                <span className="text-sm text-gray-900">{activity.event}</span>
              </div>
              <span className="text-sm text-muted">{activity.time}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default Home

